body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
  background-color: #706C61;
}

nav {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-pack: distribute;
      justify-content: space-around;
  background-color: #7aba68;
  padding: 20px 0;
}

nav a {
  text-decoration: none;
  color: white;
}

nav a:active {
  
}

.container {
  width: 90%;
  margin: 2.5% auto;
  padding: 2.5%;
  background-color: white;
  border-radius: 5px;
}

.weather-container {
  background: url('https://thumbs.dreamstime.com/b/weather-background-doodle-seamless-texture-illustration-symbols-collection-suns-clouds-storms-snow-34246151.jpg');
  padding: 2.5%;
  border-radius: 5px;
  margin-top: 2.5%;
  width: 40%;
  border: 1px solid black;
}

.inner-weather {
  text-align: center;
  width:30%;
  margin: auto;
  border-radius: 5px;
  background: white;
  padding: 2.5%;
  border: 1px solid black;
}

.title {
  color: purple;
  -webkit-transition: -webkit-transform 1s, -webkit-filter 1s;
  transition: -webkit-transform 1s, -webkit-filter 1s;
  -o-transition: transform 1s, filter 1s;
  transition: transform 1s, filter 1s;
  transition: transform 1s, filter 1s, -webkit-transform 1s, -webkit-filter 1s;
}

.title:hover {
  -webkit-transform: scale(.5);
      -ms-transform: scale(.5);
          transform: scale(.5);
  -webkit-filter: blur(5px);
          filter: blur(5px);
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 80px;
}

.App-header {
  background-color: #222;
  height: 150px;
  padding: 20px;
  color: white;
}

.App-title {
  font-size: 1.5em;
}

.App-intro {
  font-size: large;
}

@-webkit-keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}



